		cursor: pointer;
  &::before {
    content: ' ';
    display: block;
    width: 14px;
    height: 2px;
    background: $main_color;
    transition: 0.5s all; }

  &::after {
    content: ' ';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    height: 14px;
    width: 2px;
    background: $main_color;
    transition: 0.5s all; }
  &:hover {
    &::before, &::after {
      background: $second-color; } } } }





