@media (max-width: 991px) {
    .menu {
        justify-content: center;
        .menu_item {
            padding-right: 8px; }
        .menu_link {
            font-size: 11px;
            padding-left: 10px; } }
    .subheader {
        .subheader_logo {
            img {
                width: 100%; } }
        .subheader_offical {
            font-size: 11px; }
        .subheader_phone {
            font-size: 19px; }
        .subheader_btn {
            width: 100%;
            height: 40px;
            margin-top: 21px; }

        .xs-visable {
            display: none; } }
