@media (max-width: 767px) {
    .menu {
        position: fixed;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        top: 0;
        left: -100%;
        width: 50%;
        height: 100vh;
        padding: 20px 15px;
        background: rgba(0, 0, 0, .8);
        transition: 0.6s all;
        &_active {
            left: 0; }
        .menu_item {
            padding-right: 0;
            border: none;
            margin: 10px 0px;
            .menu_link {
                font-size: 20px; } } }
    .hamburger {
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        width: 26px;
        height: 12px;
        span {
            display: block;
            height: 2px;
            width: 100%;
            background-color: white;
            margin-bottom: 2.5px;
            transition: 0.5s all; }
        &_active {
            span {
                margin: -2px;
                &:nth-child(1) {
                    transform: translateY(3px) rotate(-45deg); }
                &:nth-child(2) {
                    display: none; }
                &:nth-child(3) {
                    transform: translateY(3px) rotate(45deg); } } } }

    .subheader {
        .xs-visable {
            display: block;
            text-align: center;
            display: block;
            font-weight: 300;
            font-size: 22px;
            text-align: right;
            color: #FFFFFF;
            text-transform: uppercase;
            line-height: 24px; }
        .xs-hidden {
            display: none; }

        .subheader_phone {
            display: block;
            margin-top: 10px;
            color: #fff;
            font-size: 16px;
            font-weight: 400;
            text-transform: {}
            text-align: center; }
        .subheader_btn {
            display: block;
            width: 200px;
            height: 40px;
            margin: 0 auto;
            margin-top: 8px; } }

    .promo {
        .promo_header {
            line-height: 45px; } }

    body .review {
        background: #070716;
        min-height: 393px;
        .review_img {
            height: 240px;
            width: 350px; }
        .review_descr {
            color:#fff {} } }
    footer {
        .footer_wrapper {
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .footer_wrapper>div {
 }                // justify-content: center

            .footer_three_block {
                order: 2;
                margin: 0 auto;
                width: auto;
                margin-top: 10px; }
            .footer_one_block {
                order: -1;
                // margin-left: -7px
                width: auto; } } } }



